import React from "react";
import "../../src/App.css";
import Prihodi from "./Prihodi";
import Rashodi from "./Rashodi";
import axios from "axios";
import { Link } from "react-router-dom";
import moment from "moment";
import Filter from "./Filter";

class Home extends React.Component {
  state = {
    token: localStorage.getItem("usertoken"),
    sviprihodi: [],
    svirashodi: [],
    prihodiirashodi: [],
    filter: "false",
    filterItems: [],
    isLoading: true,
    prihodi: 0,
    rashodi: 0,
  };

  componentDidMount() {
    let dateString = moment().format("ll");

    const username = localStorage.getItem("username");
    let mjesec = dateString.slice(0, 3);
    let godina = dateString.slice(7);
    axios
      .post(
        "https://racunko.herokuapp.com/filter",

        {
          username: username,
          month: mjesec,
          year: godina,
        }
      )
      .then((response) => {
        this.setState({
          isLoading: false,
        });
      })
      .catch((err) => console.log(err));
  }

  getFilterData = (dateString) => {
    let godina = dateString.slice(0, 4);
    let mesec = dateString.slice(5);

    let mjesec = moment()
      .month(parseInt(mesec - 1))
      .format("MMM");
    /* axios */

    const username = localStorage.getItem("username");
    axios
      .post("https://racunko.herokuapp.com/filter", {
        username: username,
        month: mjesec,
        year: godina,
      })
      .then((response) => {
        this.setState({ filterItems: response.data });
      })
      .catch((err) => this.props.history.push("/login"));
  };
  handleFilter = () => {
    this.setState({ filter: "true" });
  };
  handleHome = () => {
    this.setState({ filter: "false" });
  };
  podesiPrihod2 = (prihod) => {
    this.setState({
      prihodi: this.state.prihodi + prihod,
    });
  };

  podesiPrihod = (prihod) => {
    this.setState({
      prihodi: prihod,
    });
  };

  podesiRashod2 = (rashod) => {
    this.setState({
      rashodi: this.state.rashodi + rashod,
    });
  };

  podesiRashod = (rashod) => {
    this.setState({
      rashodi: rashod,
    });
  };
  renderContent() {
    if (this.state.token === null || this.state.token === "undefined") {
      this.setState({ token: null });
      this.props.history.push("/login");
    } else {
      return;
    }
  }

  renderAll() {
    if (this.state.isLoading) {
      return (
        <div className="ui segment">
          <div className="ui active inverted dimmer">
            <div className="ui large text loader"> Loading </div>{" "}
          </div>{" "}
        </div>
      );
    } else {
      return <h1>DAAAAAAAAAA</h1>;
    }
  }

  handleLogOut = () => {
    localStorage.removeItem("usertoken");
    const usertoken = localStorage.getItem("usertoken");
    this.setState({ token: null });
  };

  renderLogOut() {
    if (this.state.token) {
      return (
        <Link type="submit" onClick={this.handleLogOut}>
          Logout{" "}
        </Link>
      );
    }
  }

  render() {
    console.log(this.state.prihodi);
    console.log(this.state.rashodi);
    return (
      <React.Fragment>
        {this.state.isLoading === true ? (
          <div className="uisegment">
            <div className="ui active inverted dimmer">
              <div className="ui large text loader"> Loading </div>{" "}
            </div>{" "}
          </div>
        ) : (
          <div className="container">
            <div className="ui secondary  menu">
              <a className="active item" onClick={this.handleHome}>
                Home{" "}
              </a>{" "}
              <a className="item" onClick={this.handleFilter}>
                ByMonth{" "}
              </a>{" "}
              <div className="right menu">
                <div className="item">
                  <a className="ui item"> {this.renderLogOut()} </a>{" "}
                </div>{" "}
              </div>{" "}
            </div>{" "}
            {this.renderContent()}
            <div className="wrap">
              {" "}
              {/* <span className="logo">
                                            <img
                                              src="data:image/jpeg;base64,"
                                              id="round"
                                              alt="slika"
                                            />
                                          </span>{" "} */}
              {this.state.filter === "true" ? (
                <Filter
                  items={this.state.filterItems}
                  getFilterData={this.getFilterData}
                ></Filter>
              ) : (
                <div>
                  <span className="date">{moment().format("DD MMMM YYYY")}{" "}</span>{" "}
                  <span className="header" style={{ marginBottom: 10 }}>
                    {" "}
                    {moment().format("MMMM")}{" "}
                  </span>{" "}
                  <span className="thisUsername">@{localStorage.getItem("username")}</span>
                  <div className="half-wrap">
                    <div className="half1">
                      <Prihodi
                        podesiPrihod={this.podesiPrihod}
                        podesiPrihod2={this.podesiPrihod2}
                      />{" "}
                    </div>{" "}
                    <div className="half2">
                      <Rashodi
                        podesiRashod={this.podesiRashod}
                        podesiRashod2={this.podesiRashod2}
                      />{" "}
                    </div>{" "}
                  </div>{" "}
                  <div
                    className={
                      this.state.prihodi - this.state.rashodi > 0
                        ? "green  container-btn balans"
                        : this.state.prihodi - this.state.rashodi < 0 ?

                        "red container-btn balans" :
                        "container-btn balans"
                    }
                  >
                    {" "}
                    {this.state.prihodi - this.state.rashodi}
                    {`\u20AC`}
                  </div>{" "}
                </div>
              )}{" "}
            </div>{" "}
          </div>
        )}
      </React.Fragment>
    );
  }
}

export default Home;
